"use client"
import React, { useState } from 'react'
import { venueImages } from '../content/site'

export default function Gallery({ small = false }: { small?: boolean }) {
  const [open, setOpen] = useState<string | null>(null)
  const images = venueImages
  return (
    <div>
      <div className={`grid ${small ? 'grid-cols-3 gap-2' : 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4'}`}>
        {images.map((src) => (
          <button key={src} className="overflow-hidden rounded" onClick={() => setOpen(src)} aria-label={`Open image ${src}`}>
            <img src={src} alt="Venue interior" className={`w-full h-40 object-cover ${small ? '' : 'hover:scale-105 transition-transform'}`} />
          </button>
        ))}
      </div>
      {open && (
        <div role="dialog" aria-modal="true" className="fixed inset-0 bg-black/80 flex items-center justify-center z-50" onClick={() => setOpen(null)}>
          <img src={open} alt="Venue full" className="max-h-[90vh] max-w-[90vw] rounded" />
        </div>
      )}
    </div>
  )
}
